import { createRouter, createWebHistory } from 'vue-router';
import App from '../App.vue';   // Assuming App.vue is your main layout component
import AuthPage from '../components/AuthPage.vue'; // Ensure the path is correct

const routes = [
  {
    path: '/',
    name: 'Home',
    component: App
  },
  {
    path: '/auth',
    name: 'Auth',
    component: AuthPage
  }
];

const router = createRouter({
  history: createWebHistory(),
  routes
});

export default router;
